@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  text-decoration: none;
}
body {
  background: #fefefe;
  font-family: 'Ubuntu', sans-serif;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.2em;
  margin: 0;
  font-weight: bold;
}
button {
  -webkit-appearance: none;
          appearance: none;
  background: transparent;
  text-transform: none;
  border: none;
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}
img {
  max-width: 100%;
}
aside {
  display: block;
  margin: 0;
}
input {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}
footer {
  display: block;
  margin: 0;
}
.loader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header {
  position: fixed;
  height: 100px;
  left: 0px;
  right: 0px;
  top: 0px;
  padding: 1rem 3rem;
  background: #ffffff;
  border: 1px solid #eeeeee;
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.05);
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  z-index: 30;
}
h2,
h3 {
  font-family: 'Ubuntu', sans-serif;
}

.logo img {
  width: 100%;
}
.logo-mobile {
  display: none;
}
