.search {
  display: flex;
  width: 40%;
}
.search-input {
  background: #ffffff;
  border: 1px solid #dddddd;
  width: 100%;
  padding: 0.6rem 1.2rem;
  outline: none;
}
.search-button {
  padding: 0.6rem;
  background: #f9f9f9;
  border-right: 1px solid grey;
  border-top: 1px solid grey;
  border-bottom: 1px solid grey;
}
.mobile-search-button {
  display: none;
  outline: none;
}
.icons {
  display: flex;
  align-items: center;
}
.icons .books-icon {
  flex: none;
  margin-right: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.8rem;
  background-color: #f9f9f9;
  border-radius: 100%;
}
.icons .cart-icon {
  flex: none;
  position: relative;
  cursor: pointer;
}
.icons .cart-icon span {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  color: #fff;
  font-weight: 500;
  right: 0;
  height: 1rem;
  width: 1rem;
  font-size: 0.75rem;
  line-height: 1rem;
  background-color: #66c100;
  border-radius: 100%;
}
.mobile-search-overlay {
  position: fixed;
  left: 0;
  top: 0;
  height: 5rem;
  width: 100%;
  display: none;
  z-index: 50;
}
.mobile-search-overlay .mobile-search-container {
  display: flex;
  align-items: center;
  padding: 1rem 1rem;
  width: 100%;
  height: 5rem;
  border-bottom: 1px solid #eeeeee;
  z-index: 100;
  box-shadow: 0px -126px 209px 100px rgba(248, 248, 248, 1);
  background-color: #fff;
}
.mobile-search-overlay .close-button {
  margin-right: 1rem;
  width: 10%;
}
.mobile-search-overlay .mobile-search-wrapper {
  display: flex;
  width: 100%;
  padding-right: 1rem;
}
.mobile-search-overlay .mobile-search-wrapper .mobile-search {
  border: 1px solid #dddddd;
  width: 100%;
  font-size: 0.9rem;
  padding: 0.5rem 1.2rem;
}
.mobile-search-overlay .mobile-search-wrapper .search-button {
  padding: 0.5rem;
  background-color: #f9f9fb;
  border-right: 1px solid #dddddd;
  border-top: 1px solid #dddddd;
  border-bottom: 1px solid #dddddd;
}
@media only screen and (max-width: 768px) {
  .header {
    padding: 1rem 1rem;
    height: 5rem;
  }
  .icons .books-icon {
    margin-right: 1rem;
    padding: 0.5rem;
  }
}

@media only screen and (max-width: 480px) {
  .header {
    padding: 1rem 1rem;
    height: 5rem;
  }
  .logo-full {
    display: none;
  }
  .logo-mobile {
    display: block;
  }
  .search {
    width: auto;
  }
  .search-input {
    display: none;
  }
  .search-button {
    display: none;
  }
  .mobile-search-button {
    display: block;
  }
  .mobile-search-overlay {
    display: block;
  }
  .icons .books-icon {
    margin-right: 1rem;
    padding: 0.5rem;
  }
}

#featured-books {
  margin-top: 7rem;
}
#featured-books .title {
  width: 100%;
  padding: 1rem 3rem;
}
#featured-books .title h4 {
  padding-bottom: 1rem;
  border-bottom: 1px solid #eeeeee;
}
.featured-container {
  height: 22rem;
  width: 100%;
  position: relative;
}
.featured-container .featured-books-wrapper {
  height: 20rem;
  width: 100%;
  display: flex;
  box-shadow: 0px 25px 44px 16px rgba(0, 0, 0, 0.28);
}
.featured-container .featured-books-wrapper .left-nav {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 1;
  background-image: linear-gradient(
    to bottom,
    rgba(242, 236, 247, 0.9),
    rgba(242, 236, 247, 0.7),
    rgba(242, 236, 247, 0.08)
  );
  border-right: 1px solid rgba(242, 236, 247, 0.5);
}
.featured-container .featured-books-wrapper .right-nav {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 1;
  background-image: linear-gradient(
    to bottom,
    rgba(242, 236, 247, 0.9),
    rgba(242, 236, 247, 0.7),
    rgba(242, 236, 247, 0.08)
  );
  border-right: 1px solid rgba(242, 236, 247, 0.5);
}
.featured-container .featured-books-wrapper .book-item {
  height: inherit;
  position: relative;
  width: 100%;
  height: 20rem;
}
.book-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.book-item:hover .hoverBook {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  transition: all 0.5s ease-in-out 0.2s;
  background-image: linear-gradient(
    to top,
    rgba(0, 0, 0, 1),
    rgba(0, 0, 0, 0.6)
  );
}
.top-section {
  padding: 1.5rem;
  color: white;
}
.top-section .status {
  font-size: 0.8rem;
  color: #66c100;
}
.top-section .author {
  margin-top: 0.5rem;
}
dt {
  margin-top: 1.1rem;
  letter-spacing: 0.02rem;
  font-size: 0.8rem;
  font-weight: bold;
}
dl {
  font-size: 0.8rem;
  margin-top: 0.3rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.bottom-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  padding: 0 1.5rem;
}
.bottom-section .left-side {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 40%;
}
.bottom-section .left-side .stats {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.bottom-section .left-side .stats span {
  font-size: 0.8rem;
}
@media only screen and (max-width: 480px) {
  #featured-books {
    margin-top: 6rem;
  }
  #featured-books .title {
    display: none;
  }
  .top-section {
    padding: 1rem 1rem;
  }
  .bottom-section {
    padding: 0 1rem;
  }
}

.books {
  margin-top: 4rem;
}
.title-section {
  width: 100%;
  padding: 1rem 3rem;
}
.title-section .title {
  padding-bottom: 1rem;
  border-bottom: 1px solid #eeeeee;
}
.book-wrapper {
  padding: 0rem 3rem;
  display: grid;
  width: 100%;
  grid-column-gap: 60px;
  grid-template-columns: repeat(3, 1fr);
  grid-row-gap: 60px;
}
.book-wrapper .books-item {
  background-color: white;
  height: 12rem;
  width: 100%;
  display: flex;
}
.book-wrapper .books-item:hover {
  box-shadow: 19px 28px 35px 2px rgba(0, 0, 0, 0.17);
}
.img-wrapper {
  width: 30%;
  height: 100%;
}
.img-wrapper img {
  width: 100%;
  height: 100%;
  object-fit: fill;
}
.book-details {
  width: 70%;
  padding: 0.3rem 1rem;
}
.book-details .title {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.descriptions {
  font-size: 0.8rem;
}
.descriptions .author {
  margin-top: 0.2rem;
}
.descriptions .tag {
  margin-top: -0.1rem;
}
.stat-and-rating {
  margin-top: 0.3rem;
  display: flex;
  align-items: center;
}
.stat-and-rating .left-meta {
  display: flex;
  align-items: center;
  padding-right: 1.5rem;
}
.stat-and-rating .left-meta .stat-left {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 1rem;
}
.stat-and-rating .left-meta .stat-left span {
  font-size: 0.8rem;
}
.stat-and-rating .left-meta .stat-right {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.stat-and-rating .right-meta {
  border-left: 1px solid #eeeeee;
  padding: 0 0.7em;
}
.price-instock {
  margin-top: 0.8rem;
  display: flex;
  font-size: 0.8rem;
}
.price-instock .price {
  margin-right: 1.2rem;
}
.price-instock .stock {
  color: #66c100;
}
.price-instock .out-stock {
  color: #c12402;
}
.add-cart {
  margin-top: 0.6rem;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.add-cart .icon {
  margin-right: 1rem;
}
.add-cart .title {
  font-size: 0.9rem;
  font-weight: bold;
}
@media only screen and (max-width: 768px) {
  .book-wrapper {
    grid-column-gap: 0px;
    grid-row-gap: 30px;
    grid-template-columns: 1fr;
    padding: 1rem 1rem;
  }
}
@media only screen and (max-width: 480px) {
  .books {
    margin-top: 1rem;
  }
  .title-section {
    padding: 1rem 1.5rem;
  }
  .book-wrapper {
    padding: 0rem 1rem;
    grid-column-gap: 0px;
    grid-row-gap: 20px;
    grid-template-columns: 1fr;
    padding: 1rem 1rem;
  }
  .book-wrapper .books-item {
    margin-top: 1rem;
  }
  .img-wrapper {
    width: 35%;
  }

  .book-details {
    width: 65%;
  }
  .stat-and-rating {
    margin-top: 0.1rem;
  }
  .price-instock {
    margin-top: 0.84rem;
  }
}

#cart {
  width: 100%;
  height: 100%;
  min-height: 100%;
