#search {
  margin-top: 8rem;
}
.search-head {
  width: 100%;
  padding: 1rem 3rem;
}
.search-head .title {
  padding-bottom: 1rem;
  border-bottom: 1px solid #eeeeee;
}
.search-books-wrapper {
  padding: 0rem 3rem;
  display: grid;
  width: 100%;
  grid-column-gap: 60px;
  grid-row-gap: 60px;
  grid-template-columns: 1fr 1fr 1fr;
}
.search-books-wrapper .books-item {
  background-color: white;
  height: 12rem;
  width: 100%;
  display: flex;
}
.search-books-wrapper .books-item .img-wrapper {
  width: 30%;
  height: 100%;
}
.search-books-wrapper .books-item .img-wrapper img {
  width: 100%;
  height: 100%;
  object-fit: fill;
}
@media only screen and (max-width: 480px) {
  #search {
    margin-top: 6rem;
  }
  .search-head {
    padding: 1rem 1.5rem;
  }
  .search-books-wrapper {
    grid-column-gap: 0px;
    grid-row-gap: 30px;
    grid-template-columns: 1fr;
    padding: 1rem 1rem;
  }
}
@media only screen and (max-width: 768px) {
  .search-books-wrapper {
    grid-column-gap: 0px;
    grid-row-gap: 30px;
    grid-template-columns: 1fr;
    padding: 1rem 1rem;
  }
}

